import imgAPI from '~/static/images/imgAPI'
import TestimonialCard from '../Cards/Testimonial'
import TitleIcon from '../Title/WithIcon'
import SquareParallax from '../Parallax/SingleSquare'
import axios from 'axios'
import { mapState, mapGetters, mapActions } from 'vuex'

import CardGaleria from '@/components/Cards/CardGaleria'
import youtube from '~/youtube'

export default {
  components: {
    TestimonialCard,
    TitleIcon,
    CardGaleria,
    SquareParallax,
    Slick: () => import('vue-slick')
  },
  data() {
    return {


      videoId: '6p0VM-yUpGk',
      dialog: false,
      yt: youtube,

      urlFotoUotube: '/images/youtubeone.png',
      loaded: false,
      itemvideos: [],
      testiContent: [
        {
          text: 'Sed imperdiet enim ligula, vitae viverra justo porta vel.',
          avatar: imgAPI.avatar[10],
          name: 'John Doe',
          title: 'Chief Digital Officer',
          rating: 5
        },
        {
          text:
            'Cras convallis lacus orci, tristique tincidunt magna consequat in. In vel pulvinar est, at euismod libero.',
          avatar: imgAPI.avatar[1],
          name: 'Jean Doe',
          title: 'Chief Digital Officer',
          rating: 4
        }
      ],
        playerVars: {
        autoplay: 0,
        controls: 1,
        rel: 0,
        showinfo: 1,
        mute: 0,
        origin: 'http://localhost:8001'
      },
      slickOptions: {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        arrows: false,
        slidesToScroll: 1,
        variableWidth: true,
        responsive: [
          {
            breakpoint: 1100,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 1,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 800,
            settings: {
              slidesToShow: 2
            }
          }
        ]
      }
    }
  },
  computed: {
    ...mapState(['url_base'])
  },
  mounted() {
    this.videos()
    this.loaded = true
    const props = window.innerWidth > 1400 ? 0 : 1 // div.carousel-props length for screen < 1400px and (-1) for screen > 1400px
    const lastSlide = Math.floor(
      this.testiContent.length + props - this.slickOptions.slidesToShow
    )
    setTimeout(() => {
      if (window.innerWidth > 1279 && !this.$vuetify.rtl) {
        this.$refs.slick.goTo(lastSlide)
      }
    }, 100)
  },
  methods: {
      handleVideoClose() {
      this.dialog = false
      this.player.pauseVideo()
    },
    showImgVide(id) {
        this.videoId = id
      this.dialog = true
      if (this.yt.use) {
        setTimeout(() => {
          this.player = this.$refs.youtube.player
          this.player.playVideo()
        }, 100)
      }
    },
    videos() {
      let me = this
      let tipo = 'videos'
      let url = this.url_base + 'Control/Peticiones2.php?'
      axios({
        method: 'GET',
        url: url
      })
        .then(function(response) {
          //console.log(response)
          me.itemvideos = response.data.result
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
