// coponete de Galeria
import { KinesisContainer, KinesisElement } from 'vue-kinesis'
import TitleDeco from '../Title/WithDecoration'
import Hidden from '../Hidden'
import axios from 'axios'
import { mapState } from 'vuex'
import CardGaleria from '@/components/Cards/CardGaleria'
export default {
  components: {
    TitleDeco,
    Hidden,
    KinesisContainer,
    KinesisElement,
    Slick: () => import('vue-slick'),
    CardGaleria
  },
  props: {
    data: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      loaded: false,
      visible: false,
      index: 0,

      slickOptions: {
        dots: false,
        infinite: true,
        speed: 500,
        centerMode: true,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 3000,
        slidesToShow: 1,
        slidesToScroll: 1,
        vertical: true
      },
      itemFotosOranizaciones: [],
      expertiseList: [{ id_fotoeventoesorga: 1, nombre_foto: 'dsds' }]
    }
  },
  mounted() {
    this.loaded = true
    // this.ListFotosOrganizaciones();
  },
  computed: {
    ...mapState(['url_base']),
    isDesktop() {
      const lgUp = this.$store.state.breakpoints.lgUp
      return lgUp.indexOf(this.$mq) > -1
    },
    imgs() {
      const arr = []
      this.data.map(item => {
        arr.push(this.url_base + item.photo)
      })
      return arr
    }
  },
  methods: {
    showImg(idfoto) {
      // alert("clclc")
      this.index = this.data.findIndex(x => x.id_fotoeventoesorga == idfoto)
      this.visible = true
      // alert(this.index)
    },

    handleHide() {
      this.visible = false
    },
    ListFotosOrganizaciones() {
      let me = this
      let tipo = 'Fotos'
      let url = this.url_base + 'Control/PeticionesPagina.php?tipo=' + tipo
      //   let url = "organizacionListPhotos/"+id_organizacion;
      axios({
        method: 'GET',
        url: url
      })
        .then(function(response) {
          ///  console.log(response)
          me.itemFotosOranizaciones = response.data.result
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
